.transparent {
  opacity: 0.5;
}
.timeLineBlockText {
  width: 200px;
  color: white;
  font-size: 0.6em;
  text-align: left;
  padding: 0em 0.5em 0.5em 0.5em;
}

.square-static {
  background-color: transparent;
  border: 0;
  width: 200px;
  height: 200px;
  padding: 0;
  margin: 0;
}

.square {
  position: relative;
  background-color: hsl(210, 100%, 56%);
  border: 0;
  width: 200px;
  height: 100px;
  padding: 0;
  margin: 0;
}

.pieceContainer {
  width: 200px;
  height: 300px;
}
.square:hover {
  background-color: hsl(210, 100%, 66%);
}

.blue {
  background-color: hsl(210, 100%, 56%);
}

.quarter-circle-lower-right {
  position: relative;
  width: 200px;
  height: 200px;
  margin-top: -100px;
  border-bottom: 100px;
  border-bottom-style: solid;
  border-bottom: 100px solid hsl(210, 100%, 56%);
  border-right: 100px solid hsl(210, 100%, 56%);
  border-radius: 0 0 200px 0;
  -moz-border-radius: 0 0 200px 0;
  -webkit-border-radius: 0 0 200px 0;
}
.quarter-circle-lower-left {
  position: relative;
  width: 200px;
  height: 200px;
  margin-top: -100px;
  border-bottom: 100px solid hsl(210, 100%, 56%);
  border-left: 100px solid hsl(210, 100%, 56%);
  border-radius: 0 2000px 0 0;
  -moz-border-radius: 0 0 0 200px;
  -webkit-border-radius: 0 0 0 2000px;
}
.quarter-circle-upper-right {
  position: relative;
  width: 200px;
  height: 200px;
  border-top: 100px solid hsl(210, 100%, 56%);
  border-right: 100px solid hsl(210, 100%, 56%);
  border-radius: 0px 0px 200px 1px;
  -moz-border-radius: 200px 0 0;
  -webkit-border-radius: 0 200px 0 0;
}
.quarter-circle-upper-left {
  position: relative;
  width: 200px;
  height: 200px;
  border-top: 100px solid hsl(210, 100%, 56%);
  border-left: 100px solid hsl(210, 100%, 56%);
  border-radius: 0 200px 0 0;
  -moz-border-radius: 200px 0 0 0;
  -webkit-border-radius: 200px 0 0 0;
}

.quarter-circle-lower-right:hover {
  border-bottom: 100px solid hsl(210, 100%, 66%);
  border-right: 100px solid hsl(210, 100%, 66%);
}
.quarter-circle-lower-left:hover {
  border-bottom: 100px solid hsl(210, 100%, 66%);
  border-left: 100px solid hsl(210, 100%, 66%);
}
.quarter-circle-upper-right:hover {
  border-top: 100px solid hsl(210, 100%, 66%);
  border-right: 100px solid hsl(210, 100%, 66%);
}
.quarter-circle-upper-left:hover {
  border-top: 100px solid hsl(210, 100%, 66%);
  border-left: 100px solid hsl(210, 100%, 66%);
}

